import { Component, OnInit, ComponentFactoryResolver ,ViewChild, ElementRef} from '@angular/core';
import { Router } from '@angular/router';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { Subscription, observable } from 'rxjs';
import { filter } from 'rxjs/operators';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import { Observable, Subject } from 'rxjs';
import { interval } from 'rxjs';
import { LocationAccuracy } from '@ionic-native/location-accuracy/ngx';
import { timer} from 'rxjs'
declare var google;
@Component({
  selector: 'app-sky-tricker',
  templateUrl: './sky-tricker.page.html',
  styleUrls: ['./sky-tricker.page.scss'],
})
export class SkyTrickerPage implements OnInit {
  currentColor:any;
  latone:any;
  map: any;
  lattwo:any;
  latthree:any;
  latfour:any;
  currentMapTrack = null;
  isTracking = false;
  trackedRoute = [];
  previousTracks = [];
  positionSubscription: Subscription;
  timer:any;
  km:any;
  private tick: string;
  private subscription: Subscription;
  public timeBegan = null
  public timeStopped:any = null
  public stoppedDuration:any = 0
  public started = null
  public running = false
  public blankTime = "00:00.000"
  public time = "00:00.000"
  @ViewChild('map') mapElement: ElementRef;
  constructor(private locationAccuracy: LocationAccuracy,public router: Router,public geolocation: Geolocation,private plt: Platform) {

    this.currentColor = 'light';

    
   }


   
   mapLoad() {
    this.plt.ready().then(() => {
      let mapOptions = {
        zoom: 15,
        mapTypeId: google.maps.MapTypeId.TERRAIN,
        mapTypeControl: false,
        streetViewControl: false,
        fullscreenControl: false
      }
      this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
 
      this.geolocation.getCurrentPosition().then(pos => {
        let latLng = new google.maps.LatLng(pos.coords.latitude, pos.coords.longitude);
        this.map.setCenter(latLng);
        this.map.setZoom(15);
      }).catch((error) => {
        console.log('Error getting location', error);
      });
    });
  }



  ngOnInit() {
  }

  skyTrcikerClick(){
    this.router.navigate(['/menu/first/tabs/tab1/sky-tricker/sky-tricker-details']);

  }

  update(event){
if (event.detail.checked == true){
  this.reset()
  this.start()
  this.mapLoad()
  this.setAccuracy()
  this.isTracking = true;
  this.trackedRoute = [];
  this.km=0;

  this.positionSubscription = this.geolocation.watchPosition()
    .pipe(
      filter((p) => p.coords !== undefined) //Filter Out Errors
    )
    .subscribe(data => {
      setTimeout(() => {
        this.trackedRoute.push({ lat: data.coords.latitude, lng: data.coords.longitude });
        this.redrawPath(this.trackedRoute);
      }, 0);
    });

}else{
  this.stop()
  this.positionSubscription.unsubscribe();

}


  }


  redrawPath(path) {
    if (this.currentMapTrack) {
      this.currentMapTrack.setMap(null);
    }
 
    if (path.length > 1) {
      this.currentMapTrack = new google.maps.Polyline({
        path: path,
        geodesic: true,
        strokeColor: '#f58c29',
        strokeOpacity: 1.0,
        strokeWeight: 7
      });
      this.currentMapTrack.setMap(this.map);
    }

  }





   getDistanceFromLatLonInKm(lat1,lon1,lat2,lon2) {
    var R = 6371; // Radius of the earth in km
    var dLat = this.deg2rad(lat2-lat1);  // deg2rad below
    var dLon = this.deg2rad(lon2-lon1); 
    var a = 
      Math.sin(dLat/2) * Math.sin(dLat/2) +
      Math.cos(this.deg2rad(lat1)) * Math.cos(this.deg2rad(lat2)) * 
      Math.sin(dLon/2) * Math.sin(dLon/2)
      ; 
    var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a)); 
    var d = R * c; // Distance in km
    return d;
  }
  
   deg2rad(deg) {
    return deg * (Math.PI/180)
  }


  ionicBckClick(){
    this.positionSubscription.unsubscribe();
  }




  setAccuracy(){
    this.locationAccuracy.canRequest().then((canRequest: boolean) => {
      if(canRequest) {
        // the accuracy option will be ignored by iOS
        this.locationAccuracy.request(this.locationAccuracy.REQUEST_PRIORITY_HIGH_ACCURACY).then(
          () => console.log('Request successful'),
          error => console.log('Error requesting location permissions', error)
        );
      }
    
    });
  }


  start() {
    if(this.running) return;
    if (this.timeBegan === null) {
        this.reset();
        this.timeBegan = new Date();
        
    }
    if (this.timeStopped !== null) {
      let newStoppedDuration:any = (+new Date() - this.timeStopped)
      this.stoppedDuration = this.stoppedDuration + newStoppedDuration;
    }
    this.started = setInterval(this.clockRunning.bind(this), 10);
      this.running = true;
    }


    stop() {
      this.running = false;
      this.timeStopped = new Date();
      clearInterval(this.started);
   }
    reset() {
      this.running = false;
      clearInterval(this.started);
      this.stoppedDuration = 0;
      this.timeBegan = null;
      this.timeStopped = null;
      this.time = this.blankTime;
    }

    zeroPrefix(num, digit) {
      let zero = '';
      for(let i = 0; i < digit; i++) {
        zero += '0';
      }
      return (zero + num).slice(-digit);
    }
    clockRunning(){
      let currentTime:any = new Date()
      let timeElapsed:any = new Date(currentTime - this.timeBegan - this.stoppedDuration)
      let hour = timeElapsed.getUTCHours()
      let min = timeElapsed.getUTCMinutes()
      let sec = timeElapsed.getUTCSeconds()
     // let ms = timeElapsed.getUTCMilliseconds();
    this.time =
      this.zeroPrefix(hour, 2) + ":" +
      this.zeroPrefix(min, 2) + ":" +
      this.zeroPrefix(sec, 2) + "." 
      //this.zeroPrefix(ms, 3);
    };



}
